import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "reactstrap";
import { useFormik } from "formik";
import { toast } from "react-toastify";
import loginApi from "../../../api/loginApi";
import "./styles.css";

const Login = (props) => {
  // const { history } = props;
  const [isLoading, setIsLoading] = useState(false);
  // const [values, setValues] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");
  const [successMessage, setSuccessMessage] = useState("");
  //OTP phone login

  const validate = (values) => {
    const errors = {};
    if (!values.password) {
      errors.password = "Cn nhp mt khu";
    }
    if (!values.account) {
      errors.account = "Cn nhp account /s in thoi";
    } else if (isNaN(values.account) === true) {
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.account)) {
        errors.account = "Khng ng nh dng account ";
      } else {
        return;
      }
    } else if (isNaN(values.account) === false) {
      if (!/((09|03|07|08|05)+([0-9]{8})\b)/g.test(values.account)) {
        errors.account = "Khng ng nh dng s in thoi ";
      } else {
        return;
      }
    }
    return errors;
  };

  const formik = useFormik({
    initialValues: {
      account: "",
      password: "",
    },
    validate,
    onSubmit: (values) => {
      user_login(values);
    },
  });

  async function user_login(values) {
    setIsLoading(true);
    try {
      const json = JSON.stringify({
        account: values.account,
        password: values.password,
      });
      const response = await loginApi.getAll(json);
      console.log("Response", response);
      if (!JSON.stringify(response).includes("error")) {
        localStorage.setItem("user_info", JSON.stringify(response));
        if (response.role.roleId === 1) {
          window.location.href = "/";
        } else {
          window.location.href = "/admin";
        }
      } else {
        setErrorMessage(
          "Thng tin ng nhp khng chnh xc hy kim tra li."
        );
        setSuccessMessage("");
      }
      setIsLoading(false);
    } catch (e) {
      toast.error(e.message);
    }
  }
  useEffect(() => {}, []);

  return (
    <div className="form-center">
      <form className="formFields" onSubmit={formik.handleSubmit}>
        <h2>ng nhp</h2>
        <p className="text-danger">{errorMessage}</p>
        <div className="formField">
          <label className="formFieldLabel" for="account">
            Email / S in thoi
          </label>
          <input
            id="account"
            name="account"
            type="text"
            className="formFieldInput"
            placeholder="Email / S in thoi"
            value={formik.values.account}
            onChange={formik.handleChange}
          />
        </div>
        <p className="text-warning field_validate_label">
          {formik.errors.account ? formik.errors.account : null}{" "}
        </p>
        <div className="formField">
          <label className="formFieldLabel" htmlFor="password">
            Mt khu
          </label>
          <input
            className="formFieldInput"
            id="password"
            name="password"
            type="password"
            placeholder="Mt khu"
            value={formik.values.password}
            onChange={formik.handleChange}
          />
        </div>
        <p className="text-warning field_validate_label">
          {formik.errors.password ? formik.errors.password : null}{" "}
        </p>
        <div id="recaptcha-container"></div>
        <Row>
          {/* To loading button */}
          {isLoading ? (
            <Col md="6">
              <Button type="submit" color="primary" className="float-left">
                <span
                  class="spinner-border spinner-border-sm"
                  role="status"
                  aria-hidden="true"
                ></span>{" "}
                ang ng nhp
              </Button>
            </Col>
          ) : (
            <Col md="6">
              <Button type="submit" color="primary" className="float-left">
                ng nhp
              </Button>
            </Col>
          )}
          <Col md="6" className="text-right">
            <Button color="link" className="px-0">
              Qun mt khu?
            </Button>
          </Col>
        </Row>
      </form>
      <br />
      <p>
        <a href="/">
          <icon className="fa fa-angle-left" />
          &nbsp;Trang ch{" "}
        </a>
      </p>
    </div>
  );
};

export default Login;
